import React, {useEffect, useState} from 'react';


export default {
    title: 'useState demo',

}



export const Example = () => {
    const [counter, setCounter] = useState<Date>(new Date().getDate());
    const [fake, setFake] = useState(0);

    useEffect(() => {
    setInterval(()=>setCounter(counter+1))
    },[counter])

    let formatterTime = new Intl.DateTimeFormat("ru", {
        hour: "numeric",
        minute: "numeric",
        second: "numeric"
    });

    const stringTime = `${formatterTime.format(counter)}`||<br/>

    return (
        <>
            <button onClick={() => setCounter(counter)}>coonter</button>
            <button onClick={() => setFake(fake + 1)}>fake</button>
            {counter} {fake}

            {stringTime}


        </>
    )
};












